<template>
    <div v-if="ready">
        <v-data-table
                :headers="headers"
                :items="item_list"
                class="elevation-1"
                :rows-per-page-items="[15]"
                disable-initial-sort
            >
            <template v-slot:items="props">
                <tr class="click_row" @click="select_record(props.item.id)">
                    <td><u>Details</u></td>
                    <td>{{ props.item.tag }}</td>
                    <td>{{ props.item.proc_id }}</td>
                    <td>{{ props.item.nature }}</td>
                    <td>{{ props.item.reason }}</td>
                    <td>{{ props.item.fundsrc }}</td>
                    <td>{{ props.item.total }}</td>
                </tr>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>
    </div>
</template>
<script>
export default {
    data(){
        return{
            headers: [
                { text: 'Select',value :'id',sortable: false},
                { text: 'Reimbursement',value :'tag',sortable: true},
                { text: 'Procurement',value:'proc_id',sortable: true},
                { text: 'Item',value:'nature',sortable: true},
                { text: 'Description',value:'reason',sortable: true},                
                { text: 'Fund Source',value:'fundsrc',sortable: true},
                { text: 'Total Amount',value:'total',sortable: true},
            ],
            item_list:[],
            ready:false,
        }
    },
    async created(){
        await this.fetch_records();
        this.ready = true;
    },
    async mounted(){

    },
    methods:{
        async fetch_records(){
            await axios.get('inventory/web/backend/fetch_pending_item.php') 
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.item_list = response.data;
                        }
                    }
                )
        },
        async select_record(id){
            document.getElementById('entry').value='pending_inventory';
            document.getElementById('tmp_id').value = id;
            this.$router.push('insert_item');
        }
    }    
}
</script>
<style scoped>
.click_row{
    cursor:pointer;
}
</style>


